.terms {
  margin: 120px 0;

  &__title {

  }

  &__list {

  }

  &__item {
    padding: 80px 0;

    &:not(:last-child) {
      border-bottom: 1px solid #c7c7c7;
    }
  }

  &__subtitle {
    grid-column: 1 / 3 span;
    margin: 0;
    color: var(--main-color);

    @include tablet {
      grid-column: 1 / 5 span;
    }

    @include small-tablet {
      grid-column: 1 / 12 span;
      margin-bottom: 15px;
    }

  }

  &__content {
    grid-column: 4 / 12 span;
    @include tablet {
      grid-column: 6 / 12 span;
    }
    @include small-tablet {
      grid-column: 1 / 12 span;
    }
  }

  &__text {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 22px;
      height: 22px;
      left: -28px;
      background-image: url('../img/check.svg');
      background-position: top center;
      background-repeat: no-repeat;
    }

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    @include small-tablet {
      margin-left: 30px;
    }

  }
}
