.contacts {
  margin-bottom: 170px;

  @include small-tablet {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__subtitle {
    margin-bottom: 75px;
    color: var(--light-gray-color);
    line-height: 160%;
    max-width: 650px;
  }


  // &__inner {}

  &__list {
    grid-column: 1 / 6 span;

    @include small-tablet {
      grid-column: 12 span;
    }
    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__item {
    grid-column: 1 / 12 span;
    gap: 40px;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }

  }

  &__naim {
    margin: 0;
    color: var(--main-color);
    font-weight: 400;
    margin: 0;
    margin-bottom: 25px;
    padding-left: 32px;
    background-position: left center;
    background-repeat: no-repeat;
    background-size: contain;

    &--address {
      background-image: url('../img/marker.svg');
    }

    &--phone {
      background-image: url('../img/phone-call.svg');
    }

    &--mail {
      background-image: url('../img/mail.svg');
    }

    &--soc {
      background-image: url('../img/soc.svg');
    }
  }

  &__address {
    font-style: normal;
    max-width: 420px;
    color: var(--grey--color);
  }

  &__email {
    font-weight: 400;
    line-height: 160%;
    color: var(--accent-color);
  }

  &__maps {
    width: 100%;
    height: 486px;
    // overflow: hidden;
    grid-column: 6 span;
    // width: 100%;

    @include small-tablet {
      // display: block;
      grid-column: 12 span;
      height: 300px;
    }
  }

  &__social-list {
    display: flex;
  }

  &__social-item {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}

[class*="copyrights-pane"] {
  display: none !important;
}

.menu-contacts__data--contacts {
  color: var(--accent-color);
  font-weight: 400;
  font-size: 16px;
}

.grid .contacts__list {
  row-gap: 60px;
}
