@mixin tabs {

  .tabs__nav-btn {
    background-color: inherit;
    border: none;
    color: rgb(145, 145, 145);
    cursor: pointer;
    font-weight: 700;
    font-size: 20px;
  }

  .tabs__nav-btn--active {
    // background-color: #ff0001;
    color: var(--accent-color);
    background-color: inherit;
    border: none;
  }

  .tabs__panel {
    display: none;
  }

  .tabs__panel--active {
    display: block;
  }
}
