.catalog {

  &__title {
    margin: 0;
    margin-bottom: 120px;
    margin-top: 80px;
    font-size: 44px;
    text-align: center;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      height: 1px;
      border: 1px solid #D7D7D7;
      top: 100px;
      left: 20%;
      right: 20%;
    }
  }

  &__nav {
    grid-column: 1 / 3 span;
  }

  &__goods {
    grid-column: 4 / 12 span;

    @include tablet {
      // grid-column: 4 /;
    }
  }

  &__list {

    &-title {
      font-size: 16px;
      color: #2B2B2B;
      margin: 0;
      margin-bottom: 20px;
    }
  }

  &__item {
    padding-left: 10px;
    margin-left: 10px;
    border-left: 1px solid #A3A3A3;

    &:not(:last-child) {
      padding-bottom: 15px;
    }

    &:last-child {
      margin-bottom: 40px;
    }
  }

  &__link {
    font-size: 14px;
    line-height: 120%;
    color: #4F4F4F;
  }
}

.catalog-goods {

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 6 span;
    }

    &:last-child {
      margin-bottom: 100px;
    }
  }

  &__img img {
    object-fit: cover;
    height: 270px;
    width: 100%;
    margin-bottom: 15px;
    border-radius: var(--border);
  }

  &__price {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
    color: var(--main-color);
  }

  &__title {
    margin: 0;
    display: block;
    margin-bottom: 15px;
    color: var(--main-color);
    font-weight: 400;
    font-size: 16px;
  }
}
