.call {
  display: flex;
  align-items: center;
  margin-bottom: 150px;
  height: 870px;
  background-image: url('../img/call/call.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: var(--accent-color);

  @include mobile {
    background-size: contain;
    background-position: center top;
    background-color: inherit;
    margin-bottom: 10px;
  }

  &__container {
    display: flex;
    justify-content: flex-end;
    flex: auto;

    @include small-tablet {
      justify-content: center;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 42px;
    max-width: 240px;
    font-weight: 500;
    font-size: 22px;
  }
}

.form {
  border: 1px solid #DADADA;
  background-color: var(--light-color);
  display: flex;
  flex-direction: column;
  width: 390px;
  padding: 60px 25px;
  border-radius: 4px;

  @include mobile {
    width: 100%;
    // padding: 60px 12px;
  }

  &__style {
    background: #F3F3F3;
    border: 1px solid #DADADA;
    border-radius: 4px;
    padding: 15px;

    @include mobile {
      width: 100%;

    }
  }

  &__name {
    margin-bottom: 15px;
  }

  &__call {
    margin-bottom: 15px;
  }

  &__message {
    resize: none;
    margin-bottom: 10px;
  }

  &__label {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__checkbox {
    cursor: pointer;
  }

  &__subtitle {
    margin-left: 7px;
    font-size: 14px;
    line-height: 120%;
  }

  // &__link {
  //   color: var(--accent-color);
  // }

  &__send {
    display: flex;
    justify-content: center;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;

    & span {
      margin-left: 10px;
      display: flex;
    }
  }
}
