.card-product {

  margin-top: 100px;
  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 24px;
  }

  &__main {
    margin-bottom: 90px;
  }

  &__slider {
    grid-column: 1 / 8 span;

    @include tablet {
      grid-column: 1 / 12 span;
    }
  }

  &__control {
    padding: 60px;
    grid-column: 9 / 4 span;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--light-color);
    box-shadow: 0px 5px 20px rgba(68, 68, 68, 0.12);
    border-radius: var(--border);

    @include tablet {
      grid-column: 1 / 12 span;
    }
  }

  &__order {
    margin: 0;

    margin-bottom: 25px;
    // font-weight: 600;
    font-size: 25px;
  }

  &__price {
    margin: 0;
    margin-bottom: 25px;
    font-size: 33px;
    font-weight: 700;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      width: 22px;
      height: 22px;
      left: -28px;
      background-image: url(../img/check.svg);
      background-position: top center;
      background-repeat: no-repeat;
    }
  }

  &__btn {
    // margin-bottom: 25px;
    display: flex;
    flex-direction: column;
  }

  &__basket {
    margin-bottom: 15px;
  }

  &__credit {
    margin-bottom: 40px;
  }

  &__swiper {
    max-height: 500px;

    @include mobile {
      max-height: 400px;
    }
  }

  &__tabs {
    grid-column: 1 / 12 span;

    // @include tablet {
    //   grid-column: 1 / 12 span;
    // }
  }

  // &__benefits {
  //   grid-column: 9 / 4 span
  // }

  &__panel {
    padding: 35px;
    margin-bottom: 60px;
    background-color: var(--light-color);
    box-shadow: 0px 5px 20px rgba(68, 68, 68, 0.12);
    border-radius: 4px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 60px;
  }

  &__teh {
    margin: 0;
    height: 16px;
    overflow: hidden;
    order: 1;
    margin-left: 5px;

    @include mobile {
      margin-left: 0;
  }
  }

  &__naim {
    // display: inline;
    // width: 100%;
    color: #9E9E9E;
    margin-right: 5px;
    @include mobile {
      margin-bottom: 7px;
  }
  }

  &__feature-item {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    // margin-right: 15px;

    &::before {
      content: '';
      flex-grow: 1;
      display: block;
      order: 1;
      margin-bottom: 4px;
      border-bottom: 1px dotted #9E9E9E;

      @include mobile {
          display: none;
      }
    }

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.tabs__naim {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: var(--main-color);
}


.card-benefits {

  &__item {
    position: relative;
    margin-left: 40px;

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      width: 22px;
      height: 22px;
      left: -28px;
      background-image: url(../img/check.svg);
      background-position: top center;
      background-repeat: no-repeat;
    }
  }
}


.tabs {
  @include tabs;

  &__nav {
    display: flex;
    margin-bottom: 70px;

    &-item:not(:last-child) {
      margin-right: 15px;
    }
  }
}


.benefits__list__card-product {
  margin-bottom: 70px;
}


.goods-benefits {
  @include tablet {
    display: none;
  }
}


.swiper-mini-slide {
  position: relative;
  cursor: pointer;
  // height: 150px;
}

.swiper-mini-slide img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  // height: 150px;
}

.card-product__mini {
  height: 134px;

  @include tablet {

  }

  @include small-tablet {
    height: 130px;
  }

  @include mobile {
    height: 65px;
  }
}

.card-product__swiper {
  margin-bottom: 8px;
}


.swiper-mini-slide {
  opacity: 0.2;
  transition: opacity .2s ease-in-out;

  &:hover {
    opacity: 1;
  }
}

.swiper-mini-slide.swiper-slide-thumb-active {
  opacity: 1;
}
