.hero {

  &__content {
    position: absolute;
    max-width: 638px;
    padding-top: 110px;
    pointer-events: none;
    z-index: 4;
    display: inline-block;

    @include tablet {
      max-width: 500px;
    }

    @include mobile {
      text-align: center;
      padding-top: 60px;
    }

    &>* {
      position: relative;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    color: var(--light-color);
    font-weight: 700;
    font-size: 50px;
    line-height: 120%;

    @include mobile {
      font-size: 40px;
    }
  }

  &__subtitle {
    margin: 0;
    max-width: 470px;
    margin-bottom: 30px;
    color: var(--light-color);
    line-height: 160%;

    @include tablet {
      max-width: 400px;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__btn {
    pointer-events: auto;

    @include mobile {
      padding: 16px 46px;
    }
  }

  &-slide {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: var(--accent-color);
    position: relative;
    // width: 100%;
    overflow-y: hidden;

    &::before {
      content: "";
      display: block;
      position: absolute;
      background: rgba(5, 21, 3, 0.5);
      width: 60%;
      height: 100%;
      transform: skewX(-20deg);
      transform-origin: right top;

      @include tablet {
        // transform: skewX(-17deg);
        width: 70%;
      }

      @include small-tablet {
        transform: skewX(0);
        width: 100%;
      }
    }

    // &--1 {
    //   background-image: url('../img/hero/3-hero.jpg');
    // }

    // &--2 {
    //   background-image: url('../img/hero/2-hero.jpg');
    // }

    // &--3 {
    //   background-image: url('../img/hero/1-hero.jpg');
    // }
  }

  &__img {
    height: 100%;
    width: 100%;
  }
}

