.footer {
  background-color: #171817;
  padding: 120px 0;

  &__container {
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &-logo {
    margin-bottom: 30px;
  }

  &__control {
    // grid-column: 1 / 6 span;
  }

  &__subtitle {
    color: var(--light-gray-color);
    font-size: 14px;
    line-height: 160%;
    margin-bottom: 30px;
    max-width: 380px;
  }

  &__nav-list {
    @include small-tablet {
      margin-bottom: 50px;
    }
  }

  &__nav-item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__nav-link {
    font-size: 14px;
    color: var(--accent-color);
  }

  &__contacts {
    // grid-column: 8 / 12 span;
  }

  &__data {
    margin-bottom: 30px;
  }

  &__aadress {
    margin-bottom: 30px;
    color: var(--light-gray-color);
    font-size: 14px;
    line-height: 160%;
    font-style: normal;
    max-width: 355px;
  }
}

.menu-contacts__link--footer {
  color: var(--accent-color);
}

.social {
  display: flex;
  align-items: center;

  &__item {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
