.contacts-page {
  margin: 120px 0;

  &__title {
    margin-bottom: 80px;
  }

  &__subtitle {
    margin-bottom: 60px;
    font-weight: 500;
  }

  &__body {
    margin-bottom: 80px;
  }

  &__control {
    grid-column: 1/6 span;

    @include mobile {
      grid-column: 1/12 span;
    }
  }

  &__item {
    grid-column: 1/12 span;
  }

  &__call {
    grid-column: 8/5 span;

    @include mobile {
      grid-column: 1/12 span;
    }
  }

  &__form {
    border: none;
    padding: 0;
    width: 100%;
  }

  // &__maps {
  // width: 100%;
  // height: 486px;
  // -ms-grid-column-span: 6;
  // grid-column: 6 span;
  // }
}
