.aboutus {
  margin-bottom: 170px;

  @include mobile {
    margin-bottom: 100px;
  }

  &__up {
    margin-bottom: 140px;
    align-items: center;
    position: relative;
    @include small-tablet {
      margin-bottom: 60px;
    }

    &::before {
      content: "";
      border-radius: 4px;
      display: block;
      position: absolute;
      background-image: url('../img/aboutus/aboutus.jpg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      grid-column: 1 / 6 span;
      height: 100%;
      width: 100%;

      @include small-tablet {
        grid-column: 12 span;
        height: 280px;
        position: relative;
      }
    }
  }

  &__content {
    grid-column: 7 / 6 span;

    @include small-tablet {
      grid-column: 12 span;
      grid-row: 1;
    }
  }

  &__title {
    margin-bottom: 30px;
  }

  &__descr {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__middle {
    margin-bottom: 100px;
    align-items: center;

    @include mobile {
      margin-bottom: 60px;
    }

    &-text {
      grid-column: 1 / 8 span;

      @include tablet {
        grid-column: 12 span;
        grid-column: 1 / 7 span;
      }

      @include small-tablet {
        grid-column: 12 span;
      }
    }

    &-subtitle {
      margin-bottom: 10px;
      margin-left: 80px;
      color: var(--grey--color);

      @include mobile {
        margin-left: 0;
      }
    }

    &-title {
      margin-bottom: 42px;
      line-height: 140%;
    }

    &-benefits {
      grid-column: 4 span;

      @include tablet {
        grid-column: 5 span;
      }

      @include small-tablet {
        grid-column: 3 / 8 span;
      }

      @include mobile {
        grid-column: 12 span;
      }
    }

  }


  .list-aboutus {

    &__item {
      padding: 20px 50px 20px 50px;
      background-color: #fafafa;
      color: var(--grey-color);
      box-shadow: 0px 5px 20px rgba(68, 68, 68, 0.12);
      border-radius: 4px;
      display: flex;
      flex-direction: column;
      align-items: center;
      grid-column: 4 span;
      border: 1px solid rgb(233, 233, 233);

      @include small-tablet {
        grid-column: 12 span;
      }

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &__benefits {
      color: var(--accent-color);
      font-weight: 700;
      font-size: 60px;
      line-height: 140%;
      display: block;
      // text-align: center;
    }

    &__text {
      margin: 0;
      font-weight: 500;
      color: var(--grey--color);
      text-align: center;
    }
  }

  &__bottom {
    align-items: center;

    &-text {
      grid-column: 7 / 6 span;

      @include small-tablet {
        grid-column: 12 span;
        grid-row: 1;
      }
    }

    &-benefits {
      grid-column: 4 span;

      @include tablet {
        grid-column: 5 span;
      }

      @include small-tablet {
        grid-column: 3 / 8 span;
        grid-row: 2;
      }

      @include mobile {
        grid-column: 12 span;
      }
    }

    &-subtitle {
      margin-bottom: 10px;
      margin-left: 80px;
      color: var(--grey--color);

      @include mobile {
        margin-left: 0;
      }
    }

    &-title {
      margin-bottom: 42px;
      line-height: 140%;
    }
  }
}
