.question {
  margin-bottom: 170px;

  @include mobile {
    margin-bottom: 100px;
  }

  &__title {
    margin-bottom: 50px;
  }
}

.accordion {
  &__item {
    border-top: 1px solid rgb(185, 185, 185);

    &:last-child {
      border-bottom: 1px solid rgb(185, 185, 185);
    }
  }

  &__title {
    margin: 30px 0;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    color: var(--main-color);
  }

  &__control {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    padding: 0;
    color: var(--grey--color);
    transition: opacity .3s ease-in-out, will-change .3s ease-in-out, max-height .3s ease-in-out, overflow .3s ease-in-out, padding .3s ease-in-out;
    box-sizing: content-box;
  }

  &__icon {
    transition: transform .3s ease-in-out;
    margin-left: 15px;
  }
}

.open .accordion__icon {
  transform: rotate(-45deg);
}

.open .accordion__content {
  opacity: 1;
  padding: 5px 0 30px 0;
}

