.logo {
  margin-right: 30px;

  @include tablet {
    margin: 0;
    z-index: 2;
  }
}

.header {
  padding: 15px 0;
  background-color: #fafafa;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__card {
    margin-left: 20px;

    & path {
      transition: fill .2s ease-in-out;
    }

    &:hover path {
      fill: var(--accent-color);
    }

    @include tablet {
      margin: 0;
      z-index: 2;
    }
  }

  @include tablet {
    .burger {
      display: block;
    }
  }
}

.menu {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;

  &__control {
    display: flex;
    align-items: center;

    @include tablet {
      flex-direction: column;
    }
  }

  &-catalog {
    margin-right: 25px;
    display: flex;
    align-items: center;

    @include tablet {
      margin-right: 0;
      margin-bottom: 20px;
    }

    &--active {
      background-color: #49a01d;
    }

    &__naim {
      margin-left: 10px;
    }
  }

  &-search {
    position: relative;
    margin-right: 70px;
    padding: 10px 10px;

    @include tablet {
      margin-right: 0;
      margin-bottom: 30px;
    }

    &::before {
      content: '';
      position: absolute;
      width: 1px;
      cursor: auto;
      background-color: #cecece;
      top: 8px;
      bottom: 8px;
      right: -25px;

      @include tablet {
        width: 0;
      }
    }
  }

  &-contacts {
    display: flex;
    align-items: center;

    &--centr {
      @include tablet {
        // align-items: center;
        justify-content: center;
        flex-direction: column;
      }
    }



    &__link {
      display: flex;
      align-items: center;
      color: var(--grey--color);
      background-position: left center;
      background-repeat: no-repeat;
      font-size: 14px;
      transition: color .2s ease-in-out;

      @include tablet {
        // font-size: 20px;
      }

      &:hover {
        color: var(--accent-color);
      }

      &--mar {
        @include tablet {
          margin-right: 0;
          font-size: 20px;
          margin-bottom: 15px;
        }
      }

      &:not(:last-child) {
        margin-right: 15px;


      }
    }

    &__data {
      margin-left: 5px;
    }
  }

  @include tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
    display: block;
    overflow-y: auto;
    padding: 50px;
    width: 80%;
    height: 100vh;
    background-color: var(--light-color);
    transform: translateX(-150%);
    transition: transform 0.3s ease-in-out;

    &--active {
      transform: none;

      @include tablet {
        padding: 120px 0;
      }

      @include mobile {
        width: 100%;
      }
    }
  }

  // &__card {
  //   @include tablet {
  //     // margin: 0;
  //     z-index: 2;
  //   }
  // }
}

.nav {

  &__list {
    display: flex;

    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {

    @include tablet {
      // font-size: 20px;
      margin-bottom: 60px;
    }

    &:not(:last-child) {
      margin-right: 30px;

      @include tablet {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
  }

  &__link {
    font-size: 14px;
    color: var(--grey--color);
    transition: color .2s ease-in-out;

    @include tablet {
      font-size: 20px;
    }

    &:hover {
      color: var(--accent-color)
    }
  }
}

.burger {
  @include burger;
  display: none;
}

.hero__catalog {
  position: relative;
}

.dropdown {
  position: absolute;
  width: 340px;
  background-color: #fafafa;
  top: 132%;
  left: -50px;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity .15s ease-in-out, visibility .15s ease-in-out;

  @include tablet {
    top: 70%;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--light-color);
    box-shadow: 0px 5px 20px rgba(68, 68, 68, 0.12);
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }

  &__list {
    position: relative;
    padding: 40px 50px;
  }

  &__sublist {
    position: absolute;
    width: 340px;
    padding: 40px 50px;
    background-color: #fafafa;
    top: 0;
    left: 340px;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    transition: opacity .15s ease-in-out, visibility .15s ease-in-out;

    @include tablet {
      left: 0;
      top: 100%;
      background-color: var(--light-color);
      box-shadow: 0px 5px 20px rgba(68, 68, 68, 0.12);
    }

    &--active {
      opacity: 1;
      visibility: visible;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }

  &__subitem {
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }
}
