.benefits {
  transform: translateY(-30px);
  margin-bottom: 30px;

  &__list {
    background-color: var(--light-color);
    box-shadow: 0px 5px 20px rgb(68 68 68 / 12%);
    border-radius: 4px;
    border: 1px solid rgb(233, 233, 233);
  }

  &__item {
    padding: 73px 0 25px 0;
    font-weight: 500;
    font-size: 14px;
    color: var(--grey--color);
    grid-column: 3 span;
    display: flex;
    justify-content: center;
    background-position: center top 25px;
    background-repeat: no-repeat;
    background-size: 30px;
    position: relative;

    &--1 {
      background-image: url('../img/benefits-icon/cart.svg');
    }

    &--2 {
      background-image: url('../img/benefits-icon/truck.svg');
    }

    &--3 {
      background-image: url('../img/benefits-icon/up.svg');
    }

    &--4 {
      background-image: url('../img/benefits-icon/call.svg');
    }

    @include small-tablet {
      grid-column: 6 span;
      // grid-column: 3 / 8 span;
    }

    @include mobile {
      grid-column: 12 span;

      &:not(:last-child) {

        &::before {
          content: '';
          height: 1px;
          top: 127px;
          width: 34px;
          position: absolute;
          background-color: rgb(233, 233, 233);
        }
      }

    }
  }
}
