.basket {
  padding: 30px 220px;

  @include tablet {
    padding: 30px 70px;
  }

  @include mobile {
    padding: 30px 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 24px;
  }

  &__decor {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
  }

  &__control {
    display: flex;
    margin-bottom: 40px;
    justify-content: space-between;
    align-items: center;

    @include tablet {
      flex-direction: column;
    }
  }

  &__img {
    width: 150px;
    height: 150px;
    flex-shrink: 0;
    border-radius: var(--border);

    @include tablet {
      width: 70%;
      height: 300px;
      margin-bottom: 20px;
    }

    @include mobile {
      width: 100%;
      height: 300px;
      margin-bottom: 20px;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    padding: 0 15px;
  }

  &__naim {
    margin: 0;
    margin-bottom: 15px;
  }

  &__price {
    margin: 0;
    flex-shrink: 0;
    font-size: 20px;
    font-weight: 700;
  }

  &__data {}

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__contacts {
    background: #F3F3F3;
    border: 1px solid #DADADA;
    border-radius: 4px;
    padding: 15px;
    width: 100%;
  }

  &__label {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__input {

    &-title {
      margin: 0;
      font-weight: 700;
      margin-bottom: 15px;
    }
  }



  &-radio {}

  &-text {}

  &__btn {
    // max-width: 380px;
    // text-align: center;
  }
}

.stepper {
  position: relative;
  display: flex;
  align-items: center;
  width: 130px;
  height: 45px;
  user-select: none;
  border: 1px solid #DADADA;

  @include tablet {
    width: 100%;
    margin-bottom: 20px;
  }

  &__field {
    // overflow: hidden;
    display: inline-flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 10px;
    justify-content: center;
  }

  &__input {
    border: none;
    width: 20px;
    height: 100%;
    outline: none;
    font-weight: 700;
    // max-width: 2ex;
  }

  &__text {
    font-weight: 700;
  }

  // &__btns {
  //   position: absolute;
  //   right: 2px;
  //   top: 50%;
  //   transform: translateY(-50%);
  //   display: flex;
  //   flex-direction: column;
  //   width: 20%;
  //   height: 100%;
  // }

  &__btn {
    background-color: inherit;
    border: none;
    outline: none;
    height: 100%;
    width: 50%;
    cursor: pointer;

    &--up {
      border-left: 1px solid #DADADA;
    }

    &--down {
      border-right: 1px solid #DADADA;
    }
  }
}
